/* roboto-regular - latin */
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@100;300&display=swap');

html {
    margin: 0;
    padding: 0;
    height: 100%;
    width: 100%;
}

body {
    margin: 0;
    padding: 0;
    font-family: 'Roboto', sans-serif;
    font-size: 16px;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    height: 100%;
    width: 100%;
}

#root {
    margin: 0;
    padding: 0;
    height: 100%;
    width: 100%;
}

.App {
    margin: 0;
    padding: 0;
    display: flex;
    width: 100%;
    height: 100%;
    flex-direction: column;
    justify-content: stretch;
    align-items: flex-start;
}

header {
    display: flex;
    padding: 10px;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    align-self: stretch;
    background: #699FDF;
    flex-grow: 0;
}

div.title {
    padding: 0px 16px;
    align-items: flex-start;
    align-self: start;
    flex-grow: 0;
    gap: 10px;
}

span.title-thin {
    color: #FFF;
    font-family: Roboto;
    font-size: 36px;
    font-style: normal;
    font-weight: 200;
    line-height: normal;
}

span.title-thick {
    color: #FFF;
    font-family: Roboto;
    font-size: 36px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}

/* Small screen */
@media only screen and (max-width : 640px) {
    div.title {
        display: none;
    }
}

form.search-bar {
    display: flex;
    padding: 10px;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    justify-self: stretch;
    flex-grow: 1;
    gap: 8px;
}

input#query {
    display: flex;
    height: 32px;
    min-width: 180px;
    max-width: 360px;
    justify-self: stretch;
    flex-grow: 1;
    padding: 8px 16px;
    align-items: center;
    border-radius: 8px;
    border-width: 0;
    background: #FFF;
    font-family: Roboto;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}

    input#query::placeholder {
        color: #C3D4F2;
        font-family: Roboto;
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
    }

button#submit-button {
    display: flex;
    height: 48px;
    align-items: center;
    gap: 8px;
    border-radius: 8px;
    border-width: 0;
    background: #FFF;
    color: #699FDF;
}

    button#submit-button:hover {
        cursor: pointer;
        background: #E8EEF9;
    }



img.icon {
    width: auto;
    height: 16px;
}

span.button-text {
    font-family: Roboto;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}

main {
    display: flex;
    padding: 16px;
    align-items: flex-start;
    align-content: flex-start;
    gap: 8px;
    flex: 1 0 0;
    align-self: stretch;
    flex-wrap: wrap;
    flex-grow: 1;
    background: #FFF;
}

.results {
    display: flex;
    align-items: flex-start;
    align-content: flex-start;
    gap: 8px;
    flex: 1 0 0;
    align-self: stretch;
    flex-wrap: wrap;
    flex-grow: 1;
    background: #FFF;
}

.empty-results {
    color: #C3D4F2;
    font-family: Roboto;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: normal; /*copied the placeholder*/
}

.result {
    display: flex;
    min-width: 280px;
    max-width: 632px;
    align-items: flex-start;
    flex-grow: 1;
    gap: 8px;
    border: 1px solid #C3D4F2;
}

.entry {
    display: flex;
    padding: 8px;
    flex-direction: column;
    align-items: flex-start;
    gap: 8px;
}

.headword {
    color: #2F2F30;
    font-family: Roboto;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}

.word-class {
    color: #C3D4F2;
    font-family: Roboto;
    font-size: 16px;
    font-style: italic;
    font-weight: 400;
    line-height: normal;
}

.translation {
    color: #2F2F30;
    font-family: Roboto;
    font-size: 16px;
    font-style: normal;
    font-weight: 200;
    line-height: normal;
}

footer {
    display: flex;
    padding: 8px 48px;
    justify-content: space-between;
    align-items: flex-start;
    align-self: stretch;
    background: #EDEFF2;
    flex-grow: 0;
}

.footer-link {
    color: #2F2F30;
    font-family: Roboto;
    font-size: 16px;
    font-style: normal;
    font-weight: 200;
    line-height: normal;
    text-decoration: none;
}


.justify-right {
    justify-content: right;
    text-align: right;
}


.justify-left {
    justify-content: left;
    text-align: left;
}